import { Flex, Text, Button, useBreakpointValue, Menu, MenuButton, MenuList, MenuItem } from "@chakra-ui/react";
import { ChevronDownIcon }  from "@chakra-ui/icons";
import React from "react";
import { useTranslation } from "react-i18next";
import Socials from "./Socials";
import i18n from "i18next";
import CallButton from "./CallButton";

const supportedLanguages: {
    [key: string]: {
      nativeName: string;
    };
  } = {
    en: { nativeName: 'English' },
    ee: { nativeName: 'Eesti' },
    ru: { nativeName: '' },
};

const Header = () => {
    const { t } = useTranslation();
    const isBaseSize = useBreakpointValue(
        { base: true, md: false },
        { ssr: false }
    );

    return (
        <Flex
            justifyContent="space-evenly"
            alignItems="center"
            w="100%"
            h="100%"
            py={4}
            bgColor="transparent"
            zIndex={99999}
        >
            <Text
                fontWeight="bold"
                fontSize={isBaseSize ? "lg" : "4xl"}
                color="white"
            >{t("companyName").toLocaleUpperCase()}</Text>
            {!isBaseSize && <Socials hasBoxShadow={true} />}
            <Flex gridGap={4} alignItems="center" justifyContent="center">
                <Menu>
                    <MenuButton as={Button} rightIcon={<ChevronDownIcon />} size={isBaseSize ? "sm" : "lg"}>
                        {supportedLanguages[i18n.resolvedLanguage || "en"].nativeName}
                    </MenuButton>
                    <MenuList>
                        {Object.keys(supportedLanguages).map((language) => (
                            <MenuItem
                                key={language}
                                style={{
                                    fontWeight: i18n.resolvedLanguage === language ? 'bold' : 'normal'
                                }}
                                onClick={() => i18n.changeLanguage(language)}
                            >
                                {supportedLanguages[language].nativeName}
                            </MenuItem>
                        ))}
                    </MenuList>
                </Menu>
                <CallButton />
            </Flex>
        </Flex>
    );
};

export default Header;
