import { Flex, Text, useBreakpointValue } from "@chakra-ui/react";
import React from "react";
import { useTranslation } from "react-i18next";
import CallButton from "./CallButton";
import Socials from "./Socials";

const Footer = () => {
    const { t } = useTranslation();
    const isBaseSize = useBreakpointValue(
        { base: true, md: false },
        { ssr: false }
    );
    const year = new Date().getFullYear();
    const allRightsReservedText = t("allRightsReserved", { year });

    return (
        <>
            <Flex
                justifyContent="space-evenly"
                alignItems="center"
                w="100%"
                h="100%"
                py={4}
                bgColor="gray.900"
                zIndex={99999}
            >
                {!isBaseSize && <Socials hasBoxShadow={true} />}
                <CallButton />
            </Flex>
            <Flex w="100%" bgColor="gray.900" py={8} alignItems="center" justifyContent="center">
                <Text
                    fontSize={isBaseSize ? "sm" : "md"}
                    color="white"
                >
                    {allRightsReservedText}
                </Text>
            </Flex>
        </>
    );
};

export default Footer;
